<template>
    <input class="input" :value="value" @input="onInput" />
</template>

<script>
import { debounce } from 'lodash';

export default {
  props: {
    value: String,
    debounce: Number,
  },
  methods: {
    setOnInput() {
      this.onInput = debounce(e => this.$emit('input', e.target.value), this.debounce);
    },
  },
  created() {
    this.setOnInput();
  },
};
</script>

